export default {
	data() {
		return {
			globalUser:{}
		}
	},
	onLoad() {
		var _this = this;
		//获取用户信息缓存
		_this.globalUser = _this.getGlobalUser("globalUser");
	},
	methods: {
		formSubmitNickname(e){
			var _this = this;
			//获取value的值
			var nickname = e.detail.value.nickname;
			//console.log(nickname);
			uni.request({
				url:_this.serverUrl+"/user/xxxxxx",
				data:{
					"userId":_this.globalUser.id,
					"nickname":nickname
				},
				header:{
					"headerUserId": _this.globalUser.id,
					"headerUserToken": _this.globalUser.userUniqueToken
				},
				method:"POST,",
				success(res) {
					console.log(res);
					var resData = res.data;
					if(resData.status == 200){
						//获得最新的用户数据
						var userInfo = resData.data;
						uni.setStorageSync("globalUser",userInfo);
						uni.navigateBack({
							delta:1
						})
					}else if(resData.status == 502||resData.status == 500){
						uni.showToast({
							title:res.data.msg,
							image:"",
							duration:2000
						})
					}
				}
			})
			
		}
	}
}
